<h1 id="android-conditional-resources" tabindex="-1"><a class="header-anchor" href="#android-conditional-resources" aria-hidden="true">#</a> Android Conditional Resources</h1>
<p>Resources (strings, layouts, etc.) are loaded dynamically in Android, and when you create a new one you can specify during which conditions it should be used. This is what makes adding i18n support so easy; we have a default string resource file, but also more specialized string resource files, e.g. one with string resources in Swedish, another one with string resources in Spanish, etc. You just specify that you want to have a string resource file, and if the user uses Swedish, Android gives you the Swedish string resource file, etc. This make it convenient to handle many different type of configurations (screen sizes, preferred language, font size, etc).</p>
